import React from "react";

const ClientStatus = (props) => {

	if(props.status === "UNKNOWN") {
		return <span style={{color: "red"}}>UNKNOWN</span>
	} else if(props.status === "UP") {
		return <span style={{color: "blue"}}>UP</span>
	} else if(props.status === "INGAME") {
		return <span style={{color: "green"}}>INGAME</span>
	} else {
		return <span style={{color: "red"}}>UNKNOWN</span>
	}
};

export default ClientStatus;


